___CSS_LOADER_EXPORT___.push([module.id, `/* Base Button Styling */
.btn {
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
}

/* Color Variants */
.btn.primary {
  background-color: #007bff;
  color: #fff;
}

.btn.secondary {
  background-color: #6c757d;
  color: #fff;
}

/* Inverted Styling */
.btn.inverted {
  background-color: #fff;
  color: #007bff;
  border: 2px solid #007bff;
}

/* Disabled State */
.btn:disabled {
  background-color: #e0e0e0;
  color: #b0b0b0;
  cursor: not-allowed;
}

/* Pressed State */
.btn.pressed {
  background-color: #0056b3;
  transform: scale(0.95);
}

/* Hover Effects */
.btn:hover:not(:disabled) {
  opacity: 0.9;
}

.btn.inverted:hover:not(:disabled) {
  background-color: #007bff;
  color: #fff;
}

/* Icon Styling */
.btn-icon {
  display: inline-flex;
  align-items: center;
  margin-right: 8px;
  font-size: 16px;
  line-height: 1;
}

/* Adjust spacing for right-aligned icons */
.btn-icon.right {
  margin-left: 8px;
  margin-right: 0;
}

/* Text Styling */
.btn-text {
  display: inline-block;
}

/* Flexbox for content alignment */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
